<template>
    <div id="app">
      <div class="card2">
        <div class="card2_top" style="padding: 10px 20px 10px 20px;">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <div class="left" style="float: left;">
              <el-form-item label="设备类型名称">
                <el-input v-model="formInline.user" placeholder="设备类型名称"></el-input>
              </el-form-item>
  
              <el-form-item>
                <el-button type="primary">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="info">重置</el-button>
              </el-form-item>
            </div>
            <div class="right" style="float: right;">
              <el-form-item>
                <el-button type="success" @click="$refs.elxEditable.insert({name: 'new1'})" >+新增</el-button>
              </el-form-item>
            </div>
  
          </el-form>
        </div>
        <!-- <div class="box" :v-show="ishow">
            <elx-editable ref="elxEditable" :data.sync="tableData">
               
                <elx-editable-column prop='data' label="新增设备名称" :edit-render="{name: 'ElInput'}"></elx-editable-column>
              </elx-editable>
         
        </div> -->
        <div id="card2_bottom">
          <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column prop="equipmentTypeName" label="设备类型名称">
                <div class="box" :v-show="ishow">
                    <elx-editable ref="elxEditable" :data.sync="list">
                        <elx-editable-column prop='data' label="新增设备名称" :edit-render="{name: 'ElInput'}"></elx-editable-column>
                    </elx-editable>
                </div>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
  
                <el-button @click="handleClick(scope.row)" type="text" size="small">修改
                </el-button>
                <el-button @click="handleClick(scope.row)" type="text" size="small">删除
                </el-button>
  
              </template>
            </el-table-column>
          </el-table>
          <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
            :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
            @size-change="handleSizeChange">
          </el-pagination>
        </div>
      </div>
    </div>
  </template>
  
  <script>
    import axios from 'axios'
    import Vue from 'vue'
import {
  Editable,
  EditableColumn
} from 'vue-element-extends'
import 'vue-element-extends/lib/index.css'

Vue.use(Editable)
Vue.use(EditableColumn)
    export default {
      name: '',
      data() {
        return {
          pagesize: 5,
          currpage: 1,
          form: {
            name: '',
            region: '',
  
          },
          formInline: {
            user: '',
            region: ''
          },
          list:[

          ]
        }
  
      },
      methods: {
        handleCurrentChange(cpage) {
          this.currpage = cpage;
        },
        handleSizeChange(psize) {
          this.pagesize = psize;
        },
        handleSelectionChange(val) {
          console.log(val)
        }
      },  mounted() {
      

      axios.get('http://47.94.213.70:8084/equipmentType/getAll')
    //http://106.54.54.237:8000/api/v1/home/data?type=sell&page=1
        //then获取成功；response成功后的返回值（对象）

        .then(response => {

          console.log(response);
          alert(response);
          this.list = response.data.data;
        })

        //获取失败

        .catch(error => {

          console.log(error);

          alert('网络错误，不能访问');

        })

   
  }
    }
  
  </script>
  
  <style lang="" scoped>
    .card2 .el-input__inner {
      width: 170px;
      height: 30px;
  
    }
  
    .card2 .el-form-item {
      margin-bottom: 10px;
    }
  
    .card2 .el-button {
      padding: 5px 10px;
    }
  
  
    .card2 {
      text-align: center;
      width: 95%;
      margin: auto;
      background: white;
  
    }
  
    .card2 .bottom {
      text-align: center;
      padding: 20px;
    }
  
  </style>
  


